@import url(https://fonts.googleapis.com/css?family=Lilita+One);
/* Global */
/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */

body {
  background-image: url(/images/light-veneer.png);
  font-family: 'Lilita One', cursive;
  margin: 0;
  padding: 0;
}
.container {
  width: 80%;
  height: 100%;
  margin: auto;
  overflow: hidden;
}
.clr {
	clear:both;
}
h4 {
  display: inline;
}
p {
  font-family: "Times New Roman", "Times", serif;
}

/* Header */
/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */
header {
  margin: auto;
  text-align: center;
}
header a {
  text-decoration: none;
}
#title {
  font-size: 55px;
  font-weight: 100;
  text-shadow: 1px 1px 1px black!important; 
	color: #a848c2;
  margin-bottom: -50px;
  margin-top: 30px;
  padding: 0px 50px;
}
nav {
	color: black;
  font-size: 24px;
}
nav ul {
  list-style: none;
  padding: 0px;
}
nav li {
  padding: 30px;
  padding-top: 40px;
  padding-bottom: 20px;
  display: inline-block;
}
.nav-item {
  color: black;
	text-decoration: none;
}
.nav-item:hover {
  color: purple;
}
.active {
  color: purple;
  text-decoration: underline;
}

/* Main */
/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */
.sidebar {
  padding: 30px;
  text-align: center;
  width: 100%;
  float: left;
  box-sizing: border-box;
/*  max-width: 250px;*/
  min-width: 180px;
}
.sidebar p {
  font-size: 13px;
}
.sidebar a {
  text-decoration: underline;
  color: #000;
}
.sidebar a:hover {
  color: purple;
}
.info {
  width: 25%;
  height: 100px;
  font-size: 15px;
  text-align: left;
  float: left;
  box-sizing: border-box;
}
#purple {
  color: #a848c2;
}
.content {
  padding: 40px;
  padding-top: 20px;
/*  float: center;*/
  margin: auto;
  width: 100%;
/*  min-width: 600px;*/
  max-width: 900px;
/*  box-sizing: border-box;*/
/*
  border: solid black 1px;
  background-color: red;
*/
}
.content li {
	padding: 10px;
  
}
iframe {
  width: 100%;
  height: 100%;
/*  margin: 0 auto;*/
  float: left;
  margin-top: 20px;
  height: 120rem;
  border: none;
/*  padding-bottom: 100px;*/
}
img {
  margin: auto;
  padding-top: 20px;
  padding-bottom: 40px;
  width: 100%;
/*  float: left;*/
}

/* Menus */
/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */
.menuContainer {
  width: 70%;
  max-width: 800px;
  margin: 0 10%;
}
#menuNav {
  position: -webkit-sticky;
  position: sticky;
  float: right;
  background-color: #ffffd0;
  width: 20%;
/*  margin-top: 30px;*/
  top: 0;
/*  right: 0;*/
  padding: 10px;
  border: 4px solid white;
  box-sizing: border-box;
}
#menuNav h4 {
  color: #a848c2;
  font-size: 1.2em;
}
#menuNav a {
  text-decoration: none;
  color: black;
  display: block;
  font-size: 1em;
  padding: 10px 0px;
  border-bottom: dotted 1px #ffd0fa;
  box-sizing: border-box;
}
/* Footer */
/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */
footer {
  font-size: 11px;
  text-align: center;
  color: black;
  padding: 20px;
  margin-top: 30px;
  height: 3rem;
}

