@media screen and (max-width: 768px) {
  header {
    max-width: 475px;
  }
   #title {
    font-size: 42px;
  }
  nav {
    width: 100%;
    font-size: 15px;
/*    border: dotted 1px black;*/
    padding-bottom: 0;
  }
  nav li {
    padding: 10px;
    padding-top: 40px;
  }
  
  .sidebar {
    width: 100%;
    margin: 0;
  }
  .sidebar p {
    font-size: 12px;
  } 
  footer {
    margin: 0;
    padding: 0;
  }
  
  .info {
    width: 50%;
    float: left;
    box-sizing: border-box;
    text-align: center;
    font-size: 0.9em;
  }
  img {
    width: 100%;
  }
  #menuNav h4 {
    font-size: 0.7em;
  }
